<template>
  <div class="inputBox shadow">
    <input type="text" v-model="newTodoItem" v-on:keyup.enter="addTodo">
    <!-- <button v-on:click="addTodo">추가</button> -->
    <span class="addContainer" v-on:click="addTodo">
      <i class="fas fa-plus addBtn"></i>
    </span>
    <Modal v-if="showModal" @close="showModal = false">
      <h3 slot="header">
        경고!
        <i class="fas fa-times closeModalBtn" @click="showModal=false"></i>
      </h3>
      <div slot="body">
          할일(todo)를 입력하세요!
      </div>
    </Modal>
  </div>
</template>

<script>
import Modal from './commons/Modal.vue';

export default {
  data: function() {
    return {
      newTodoItem: "",
      showModal: false
    };
  },
  components: {
    Modal
  },
  methods: {
    addTodo() {
      if (this.newTodoItem !== "") {
        //this.$emit("addTodoEvt", this.newTodoItem);
        const itemObj = {completed:false, item:this.newTodoItem}
        this.$store.dispatch('addTodoItems',itemObj);
        this.clearInput();
      }else {
        this.showModal = !this.showModal;
      }
    },
    clearInput() {
      this.newTodoItem = "";
    }
  }
};
</script>

<style scoped>
input:focus {
  outline: none;
}
.inputBox {
  background: white;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
}
.inputBox input {
  border-style: none;
  font-size: 0.9rem;
}
.addContainer {
  float: right;
  background: linear-gradient(to right, #6478fb, #8763fb);
  display: block;
  width: 3rem;
  border-radius: 0 5px 5px0;
}
.addBtn {
  color: white;
  vertical-align: middle;
}
.closeModalBtn{
color: #42b983;
}
</style>
